}

h1 {
  text-align: center;
  color: var(--yellow);
}

.drag-drop {
  border: 2px dashed var(--border);
  text-align: center;
  background-color: var(--bg1);
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  padding: 20px;
  display: flex;
}

.drag-drop-text {
  color: var(--gray);
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
}

.file-input-label {
  display: none;
}

.file-input-label + .file-input-display {
  color: var(--fg);
  margin-top: 10px;
  font-size: 14px;
}

.file-input-label:hover {
  text-decoration: underline;
}

.file-input-label input[type="file"] {
  display: none;
}

.drag-drop p {
