  box-shadow: 0 0 3px var(--yellow);
  outline: none;
}

button {
  background-color: var(--aqua);
  color: var(--bg);
  cursor: pointer;
  font-size: 16px;
  font-family: var(--font-family-monospace);
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px 15px;
  transition: background-color .3s;
}

button:hover {
  background-color: var(--green);
  font-family: var(--font-family-monospace);
}

